var React = require('react');

var bonnerImage = require('../assets/bonner.jpg');
var bonnerCss = require('../src/bonner.css');

var Bonner = React.createClass({displayName: 'Bonner',
  propTypes: {
    tema: React.PropTypes.string.isRequired
  },

  render:function() {
    return (
      React.DOM.main(null, 
        React.DOM.span({className: "tema"}, this.props.tema), 
        React.DOM.img({src: '../dist/' + bonnerImage})
      )
    );
  }
});

module.exports = Bonner;



